.App {
  text-align: center;
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
}

a {
  color: #18272F;
  position: relative;
  text-decoration: none;
}

a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #18272F;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  .banner{
    max-width: 50%;
  }
}

.App-navbar {
  display: flex;
  padding-top: 40px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: "Courier New";
}

.App-footer {
  display: flex;
  padding-top: 40px;
  padding-bottom: 40px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: "Courier New";
}

.App-header {
  background-color: #FAF9F6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: "Courier New";
  line-height: 1;
}

.App-link {
  color: #61dafb;
}

.banner {
  max-width: 80%;
  height: auto;
  padding-top: 50px;
}

.banner-partners {
  width: auto;
  height: 550px;
  padding: 50px;
}

.banner-menu {
  width: 50%;
  height: auto;
  padding-top: 50px;
}

.banner-support {
  width: auto;
  height: 500px;
  padding: 50px;
}


td {
  padding: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
